@use "sass:map";
@use "../globals/variables" as *;
@use "../utilities/breakpoints" as *;

//display property
@each $key, $value in $display {
  .display-#{$value} {
    display: $value;
  }
}

// Display-flex
@each $key, $value in $flex-display-map {
  .d-#{$key} {
    display: $value;
  }
}

// Flex Direction
@each $key, $value in $flex-direction-map {
  .flex-#{$key} {
    flex-direction: $value;
  }
}

// Justify Content
@each $key, $value in $justify-content-map {
  .justify-#{$key} {
    justify-content: $value;
  }
}

// Align Items
@each $key, $value in $align-items-map {
  .items-#{$key} {
    align-items: $value;
  }
}

// Align Content
@each $key, $value in $align-content-map {
  .content-#{$key} {
    align-content: $value;
  }
}

// Flex Wrap
@each $key, $value in $flex-wrap-map {
  .flex-#{$key} {
    flex-wrap: $value;
  }
}

//grid

// Display
@each $key, $value in $grid-display-map {
  .d-#{$key} {
    display: $value;
  }
}

// Grid template columns
@each $key, $value in $grid-template-columns-map {
  .grid-cols-#{$key} {
    grid-template-columns: $value;
  }
}

// Grid template rows
@each $key, $value in $grid-template-rows-map {
  .grid-rows-#{$key} {
    grid-template-rows: $value;
  }
}

// Column gap
@each $key, $value in $grid-column-gap-map {
  .gap-col-#{$key} {
    column-gap: $value;
  }
}

// Row gap
@each $key, $value in $grid-row-gap-map {
  .gap-row-#{$key} {
    row-gap: $value;
  }
}

// Justify items
@each $key, $value in $justify-items-map {
  .justify-items-#{$key} {
    justify-items: $value;
  }
}

