@use "../globals/variables" as *;
@use "../utilities/breakpoints" as *;
@use "sass:map";

@each $key, $value in $font-weights {
  .fw-#{$key} {
    font-weight: $value;
  }
}

@each $key, $value in $font-size {
  .fs-#{$key} {
    font-size: $value;
  }
}

@each $key, $value in $text-styles {
  .text-align-#{$value} {
    text-align: $value;
  }
}

//xs
@include xs {
  @each $key, $value in $font-weights {
    .fw-#{$key}-xs {
      font-weight: $value;
    }
  }

  @each $key, $value in $font-size {
    .fs-#{$key}-xs {
      font-size: $value;
    }
  }

  @each $key, $value in $text-styles {
    .text-align-#{$value}-xs {
      text-align: $value;
    }
  }
}

