@use '../globals/variables' as *;
@use '../utilities/breakpoints' as *;

//padding classes
@each $key, $value in $padding-margin {
    .p-#{$key} {
        padding: $value;
    }

    .pt-#{$key} {
        padding-top: $value;
    }

    .pl-#{$key} {
        padding-left: $value;
    }

    .pb-#{$key} {
        padding-bottom: $value;
    }

    .pr-#{$key} {
        padding-right: $value;
    }

    .p-inline-#{$key} {
        padding-inline: $value;
    }

    .p-block-#{$key} {
        padding-block: $value;
    }
};

@each $key, $value in $padding-margin {
    .m-#{$key} {
        margin: $value;
    }

    .mt-#{$key} {
        margin-top: $value;
    }

    .ml-#{$key} {
        margin-left: $value;
    }

    .mb-#{$key} {
        margin-bottom: $value;
    }

    .mr-#{$key} {
        margin-right: $value;
    }

    .m-inline-#{$key} {
        margin-inline: $value;
    }

    .m-block-#{$key} {
        margin-block: $value;
    }
};


