@use "../utilities/functions" as *;

//Colors
$Orange: #fd6f00;
$black: #000000;
$white: #ffffff;

//color themes

//light theme
$light-theme: (
  "primary-color": #fd6f00,
  "secondary-color": #000000,
  "tertiary-color": #eeeeee,
);

//light theme
$dark-theme: (
  "primary-color": #fd6f00,
  "secondary-color": #f1f1f1,
  "tertiary-color": #191919,
);

$orange-shades: (
  "orange-50": #ffefe6,
  "orange-100": #ffcfb0,
  "orange-200": #ffb78a,
  "orange-300": #ff9654,
  "orange-400": #ff8233,
  "orange-500": #ff6300,
  "orange-600": #e85a00,
  "orange-700": #b54600,
  "orange-800": #8c3600,
  "orange-900": #6b2a00,
);

$gray-shades: (
  "gray-50": #e6e6e6,
  "gray-100": #b0b0b0,
  "gray-200": #8a8a8a,
  "gray-300": #545454,
  "gray-400": #333333,
  "gray-500": #000000,
  "gray-600": #000000,
  "gray-700": #000000,
  "gray-800": #000000,
  "gray-900": #000000,
);

//fonts
$poppins: "Poppins", sans-serif;

//font-weight
$font-weights: (
  "100": 100,
  "200": 200,
  "300": 300,
  "400": 400,
  "500": 500,
  "600": 600,
  "700": 700,
  "800": 800,
  "900": 900,
);
//font sizes

$base-fs: 1rem;

$font-size: (
  "10": rem(10),
  "12": rem(12),
  "14": rem(14),
  "16": rem(16),
  "18": rem(18),
  "20": rem(20),
  "22": rem(22),
  "24": rem(24),
  "26": rem(26),
  "28": rem(28),
  "32": rem(32),
  "38": rem(38),
  "40": rem(40),
  "44": rem(44),
  "55": rem(55),
  "69": rem(69),
  "80": rem(80),
  "100": rem(100),
);

//padding-margin

$base-padding: 1rem; //16px
$base-margin: 1rem; //16px

$padding-margin: (
  "50": rem(5),
  "80": rem(8),
  "100": rem(10),
  "150": rem(15),
  "200": rem(20),
  "250": rem(25),
  "300": rem(30),
  "350": rem(35),
  "400": rem(40),
  "450": rem(45),
  "500": rem(50),
  "550": rem(55),
  "600": rem(60),
  "650": rem(65),
  "700": rem(70),
  "750": rem(75),
  "800": rem(80),
  "850": rem(85),
  "900": rem(90),
  "950": rem(95),
  "1000": rem(100),
);

//border

$base-border: rem(2);

$border: (
  "1": rem(1),
  "2": rem(2),
  "3": rem(3),
  "4": rem(4),
  "5": rem(5),
  "6": rem(6),
  "7": rem(7),
  "8": rem(8),
  "9": rem(9),
  "10": rem(10),
);

$text-styles: (
  '1': center,
  '2': left,
  '3': right,
  '4': justify,
);


//breakpoints
$breakpoints: (
  "xs" : 0,
  "sm" : em(576),
  "md" : em(768),
  "lg" : em(992),
  "xl" : em(1200),
  "xxl" : em(1400)
);

//Display
$display : (
  "1" : none,
  "2" : block,
  "3" : inline,
  "4" : inline-block,
  "5" : flex,
  "6" : grid,
);


$flex-display-map: (
  flex: flex,
  inline-flex: inline-flex
);

$flex-direction-map: (
  row: row,
  row-reverse: row-reverse,
  column: column,
  column-reverse: column-reverse
);

// $justify-content-map: (
//   start: flex-start,
//   end: flex-end,
//   center: center,
//   between: space-between,
//   around: space-around,
//   evenly: space-evenly
// );

// $align-items-map: (
//   start: flex-start,
//   end: flex-end,
//   center: center,
//   baseline: baseline,
//   stretch: stretch
// );

// $align-content-map: (
//   start: flex-start,
//   end: flex-end,
//   center: center,
//   between: space-between,
//   around: space-around,
//   stretch: stretch
// );

$flex-wrap-map: (
  wrap: wrap,
  nowrap: nowrap,
  wrap-reverse: wrap-reverse
);

//grid
// _grid-maps.scss

$grid-display-map: (
  grid: grid,
  inline-grid: inline-grid
);

$grid-template-columns-map: (
  '1': 1fr,
  '2': repeat(2, 1fr),
  '3': repeat(3, 1fr),
  '4': repeat(4, 1fr),
  '5': repeat(5, 1fr),
  '6': repeat(6, 1fr)
);

$grid-template-rows-map: (
  '1': 1fr,
  '2': repeat(2, 1fr),
  '3': repeat(3, 1fr),
  '4': repeat(4, 1fr)
);

$grid-column-gap-map: (
  '0': 0,
  '1': 0.25rem,
  '2': 0.5rem,
  '3': 1rem,
  '4': 1.5rem,
  '5': 3rem,
  '6': 3.5rem
);

$grid-row-gap-map: (
  '0': 0,
  '1': 0.25rem,
  '2': 0.5rem,
  '3': 1rem,
  '4': 1.5rem,
  '5': 3rem,
  '6': 3.5rem
);

$justify-items-map: (
  start: start,
  end: end,
  center: center,
  stretch: stretch
);

$align-items-map: (
  start: start,
  end: end,
  center: center,
  stretch: stretch
);

$justify-content-map: (
  start: start,
  end: end,
  center: center,
  stretch: stretch,
  'space-between': space-between,
  'space-around': space-around,
  'space-evenly': space-evenly
);

$align-content-map: (
  start: start,
  end: end,
  center: center,
  stretch: stretch,
  'space-between': space-between,
  'space-around': space-around,
  'space-evenly': space-evenly
);
