@use '../globals/variables' as *;
@use 'sass:map';

//orange shade class
@each $key, $value in $orange-shades {
    .#{$key} {
        color: $value;
    }
};

//gray shade class
@each $key, $value in $gray-shades {
    .#{$key} {
        color: $value;
    }
};


//creating color themes
.light {
    --primary-color: #{map.get($light-theme, "primary-color")};
    --secondary-color: #{map.get($light-theme, "secondary-color")};
    --tertiary-color: #{map.get($light-theme, "tertiary-color")};
}

.dark {
    --primary-color: #{map.get($dark-theme, "primary-color")};
    --secondary-color: #{map.get($dark-theme, "secondary-color")};
    --tertiary-color: #{map.get($dark-theme, "tertiary-color")};
}



