@use "../utilities/functions" as *;
@use "../utilities/breakpoints" as *;

header,
main,
footer {
  max-width: 1440px;
  background-color: var(--tertiary-color);
  margin: auto;
}

#theme-toggle {
  span {
    font-size: 50px;
  }
}

#theme-toggle {
  position: fixed;
  top: 5px;
}

header {
  a {
    color: var(--secondary-color);
  }
}


.btn-primary {
  color: var(--secondary-color);
  background-color: var(--primary-color);
  border-radius: 10px;
}

.btn-secondary {
  color: var(--secondary-color);
  background-color: rgb(199, 199, 199);
  border-radius: 10px;
}

.btn-primary,
.btn-secondary {
  transition: background-color 0.3s, color 0.3s;
}


@include breakpoint(em(615)) {
  nav {
    a {
      font-size: rem(16);
    }
  }
}

footer {
  p,
  a {
    color: var(--secondary-color);
  }
}

.hero-description {
  p {
    color: var(--secondary-color);
    letter-spacing: 1px;
    line-height: 1.5;
  }

  h1 {
    line-height: 1.2;
    color: var(--secondary-color);
  }

  h2 {
    color: var(--secondary-color);
  }

  h5 {
    color: var(--primary-color);
    letter-spacing: 1px;
  }

  h6 {
    color: var(--secondary-color);
    letter-spacing: 1px;
  }
}

.profile {
  aspect-ratio: 1;
  object-fit: cover;
  border-radius: 100%;
  background: linear-gradient(to right, red, purple);
  padding: 3px;
}

.social-links-1 {
  img {
    width: 20px;
  }

  a {
    line-height: 0;
  }
}

.Services-desciption {
  h2,
  p {
    color: var(--secondary-color);
  }
}
.card-services {
  width: 250px;
  height: 100%;
  background-color: rgb(221, 221, 221);
  border-radius: 10px;


  img {
    width: 50px;
  }

  p {
    line-height: 1.5;
  }
}

.skills {
  h2 {
    color: var(--secondary-color);
  }

  p {
    color: var(--secondary-color);
  }
}

.projects {
  h2,
  p {
    color: var(--secondary-color);
  }
}

.filters {
  a {
    color: black;
  }
}
.card-project {
  width: 350px;
  height: 100%;
  background-color: var(--tertiary-color);
  border-radius: 15px;
  
  h3,
  p {
    color: var(--secondary-color);
  }
  
  p {
    line-height: 1.5;
  }
  
  img {
    object-fit: cover;
    aspect-ratio: 0.77/1;
    border: 2px solid gray;
    border-radius: 15px;
  }
}


.contact-me {
  h2,
  p {
    color: var(--secondary-color);
  }
}

form {
  input {
    width: 300px;
    height: 35px;
    border: 2px solid gray;
    border-radius: 35px;
    outline: none;
  }

  textarea {
    width: 300px;
    height: 200px;
    border: 2px solid gray;
    outline: none;
    border-radius: 15px;
  }
}

button {
  border: none;
}

@include xs {
  .profile {
    width: 250px;
  }
}

@include sm {
  .profile {
    width: 350px;
  }

  .card-project {
    width: 450px;
  }

  form {
    input {
      width: 500px;
      height: 45px;
    }

    textarea {
      width: 500px;
      height: 250px;
    }
  }
}

@include md {
  nav {
    a {
      font-size: rem(16);
    }
  }
}

@include lg {
  form {
    input {
      width: 550px;
      height: 50px;
    }

    textarea {
      width: 550px;
      height: 250px;
    }
  }
  nav {
    a {
      font-size: rem(18);
    }
  }

  .card-services {
    img {
      width: 60px;
    }
  }
}

